.shop {
  @extend %FontFamily-Avenir;
  position: relative;
  display: flex;
  flex-direction: column;
  padding-left: 150px;
  text-transform: uppercase;
  color: $header-color;

  &__title {
    margin-bottom: 20px;
  }

  &__list {
    display: flex;
    padding: 0;
    list-style: none;
    column-gap: 20px;
  }

  &__item::after {
    display: block;
    content: "";
    height: 2px;
    width: 100%;
    margin-top: 20px;
    background-color: $header-color;
  }

  &__catalog {
    display: flex;
    flex-direction: row;
    column-gap: 20px;
    margin: 63px 0 30px;
    padding-bottom: 30px;
    overflow-x: scroll;
    scroll-behavior: smooth;

    &::-webkit-scrollbar {
      height: 9px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $second-main-color;
      border-radius: 50px;
      outline: #977272;
    }
  }

  &__card {
    width: 380px;
    cursor: pointer;
    transition-duration: 0.3s;
    flex-shrink: 0;
    &-img {
      display: block;
      object-fit: cover;
      width: 100%;
      height: 500px;
      transition: transform 0.5s;
    }
    &-name {
      margin-top: 20px;
      font-size: 20px;
      text-transform: uppercase;
    }
    &-price {
      margin-top: 10px;
      font-size: 18px;
    }
  }

  &__card:hover {
    color: #977272;
    .shop__card-img {
      transform: scale(1.03);
    }
  }

  &__button {
    @extend %button;
    align-self: center;
  }

  @media (max-width: 1200px) {
    & {
      padding: 0 27px;
      &__card {
        &-img {
          width: 100%;
          height: 300px;
        }
      }
    }
  }

  @media (max-width: 1000px) {
    & {
      overflow-x: hidden;
      &__catalog {
        margin: 20px 0 30px;
        padding-bottom: 20px;
        width: 100vw;
      }

      &__card {
        width: 47.8%;
        &-img {
          width: 100%;
          overflow: hidden;
        }
      }
    }
  }

  @media (max-width: 575px) {
    & {
      padding: 60px 20px;
      align-items: center;
      overflow-x: hidden;
      &__title {
        font-size: 24px;
        margin-bottom: 12px;
      }

      &__category {
        display: flex;
        justify-content: space-between;
      }

      &__item::after {
        margin-top: 10px;
      }

      &__catalog {
        width: 100%;
      }

      &__card {
        width: 100vw;
        &-img {
          height: 267px;
        }
        &-name {
          font-size: 20px;
        }
        &-price {
          font-size: 18px;
        }
      }
    }
  }
}
