.form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__field::placeholder {
    color: $form-color;
    text-transform: capitalize;
  }

  &__field {
    max-width: 480px;
    height: 27px;
    padding-bottom: 10px;
    border: none;
    border-bottom: 1px solid $form-color;
    background-color: $body-bgc;
    transition: color 0.3s, border-bottom 0.3s;
  }

  &__field:nth-child(-n + 2) {
    margin-bottom: 30px;
  }

  &__field:nth-child(3) {
    margin-bottom: 60px;
  }
  &__field:nth-child(4) {
    margin-bottom: 50px;
  }

  &__field:focus {
    outline: none;
    border-bottom: 1px solid #333;
  }

  &__field:valid {
    color: #344f10;
    border-bottom: 1px solid  #344f10;
  }

  //Must work with JS

  // &__field:invalid {
  //   color: #eb5757;
  //   border-bottom: 1px solid  #eb5757;
  // }

  &__button {
    font-family: inherit;
    @extend %button;
  }

  @media (max-width: 1000px) {
    & {
      padding-top: 20px;
      &__field:nth-child(-n + 2) {
        margin-bottom: 25px;
      }

      &__field:nth-child(3) {
        margin-bottom: 45px;
      }
      &__field:nth-child(4) {
        margin-bottom: 30px;
      }
    }
  }
}
